<template>
  <div id="areaManagement-wrapper">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: 100%" ref="getQuery"></QueryBox>
    </div>
    <div style="height: 100%">
      <div class="showBox">
        <transition name="fade">
          <div class="sta_box sta_box1" :class="{activeBox: boxstate === 1}">
            <div class="con_box">
              <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 :1" size="small" class="boxBtn"></Button>
              <div class="bigBox" :class="{smallToBig:boxstate === 1}">
                <div class="smallBox">
                  <BarChart :data="data1"></BarChart>
                </div>
              </div>
              <Spin size="large" fix v-if="spinShow"></Spin>
            </div>
          </div>
        </transition>
        <div class="sta_box sta_box2" :class="{activeBox: boxstate === 2}">
          <div class="con_box">
            <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 2 ? 0 :2" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 2}">
              <div class="smallBox">
                <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import BarChart from './BarChart.vue'
import {
  // 任务列表列表
  listTaskSelect
} from '@/api/dataAnalysis'
import {
  // 统计数据
  listPatrol
} from '@/api/areaManagement'
export default {
  name: 'dataAnalysis',
  data () {
    return {
      // 核查任务列表
      taskList: [],
      data1: [],
      /* 表格高度 */
      tableHeight: window.innerHeight - 295,
      // loading
      spinShow: false,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '核查人员',
          align: 'center',
          key: 'uname',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        },
        {
          title: '巡查次数(次)',
          align: 'center',
          key: 'rwcs',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        },
        {
          title: '巡查里程(m)',
          align: 'center',
          key: 'gjlc',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        },
        {
          title: '巡查时长(小时)',
          align: 'center',
          key: 'rwsc',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        }
      ],
      /* 加载 */
      loading: false,
      // 放大缩小状态
      boxstate: 0
    }
  },
  components: {
    QueryBox,
    BarChart
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 2,
          field: 'cl001',
          title: '核查任务',
          data: this.taskList
        },
        {
          type: 1,
          field: 'uname',
          title: '核查人员'
        }
      ]
    }
  },
  created () {
    this.getTasks()
    this.getTableList()
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.getTableList()
    },
    // 获取核查任务列表
    getTasks () {
      listTaskSelect({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.taskList.push({
              label: item.cl002,
              value: item.cl001
            })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取统计数据 */
    getTableList () {
      let data = ''
      this.spinShow = true
      this.loading = true
      data = {
        ...this.queryData
      }
      listPatrol({
        data: data
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              this.data1.push({
                uname: item.uname,
                rwcs: item.rwcs,
                gjlc: item.gjlc,
                rwsc: item.rwsc
              })
            })
          }
          this.spinShow = false
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #areaManagement-wrapper{
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .radioBox {
      padding: 0 15px 15px;
      text-align: left;
    }
    .showBox {
      padding: 0 15px;
      height: calc(100% - 47px);
      position: relative;
      .sta_box {
        position: absolute;
        width: 49%;
        overflow: hidden;
        height: calc(100% - 30px);
        padding: 15px;
        background: #fff;
        &.sta_box1 {
          left: 15px;
        }
        &.sta_box2 {
          right: 15px;
        }
        .con_box{
          height: 100%;
          width: 100%;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          .ivu-table-wrapper {
            border-radius: 10px;
          }
          .boxBtn{
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:100%;
            height:100%;
            .smallBox{
              width:100%;
              height:100%;
            }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
          }
        }
      }
    }
    .page_box{
      width: 100%;
      padding: 15px 15px 0;
      text-align: right
    }
    /* box放大 */
    .activeBox{
      width: calc(100% - 15px) !important;
      transition: all 0.4s ease;
      z-index: 999;
    }
  }
</style>
