import request from '@/utils/request'

/* 任务列表 */
export const listTaskSelect = data => request({ url: '/task/listTaskSelect', method: 'post', data })
/* 统计数据 */
export const listLedger = data => request({ url: '/statis/listLedger', method: 'post', data })
/* 显示问题点数、核查数 */
export const getNumbers = data => request({ url: '/statis/pointStatistics', method: 'post', data })
